.header {
  max-width: 882px;
  margin: 45px auto 40px;
  padding-bottom: 40px;
  border-bottom: 1px solid rgba(54, 54, 54, 0.7);
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 760px) and (min-width: 320px) {
  .header {
    margin: 30px auto 40px;
    padding-bottom: 30px;
  }
}
