.authorisation__container {
  position: relative;
  display: flex;
  flex-direction: column;

  color: #fff;
  border-radius: 3%;
  max-width: 430px;
  margin: 0 auto;
}

@media screen and (max-width: 520px) and (min-width: 320px) {
  .authorisation__container {
    margin: 0 19px;
  }
}
