  max-width: 1280px;
  min-width: 320px;
  margin: 0 auto;
  font-family: 'Inter', 'Arial', sans-serif;
  background-color: #000;
  box-sizing: border-box;
  color: #fff;
  -webkit-font-smoothing: antialiased;
